import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { EditAffiliationComponent } from './edit-affiliation.component';
const routes: Routes = [
  {
    path: '',
    component: EditAffiliationComponent,
    data: {
      title: 'Edit Affiliation'
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EditAffiliationRoutingModule {}



