import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from "@angular/forms";
import { HttpModule } from "@angular/http";
import { EditAffiliationComponent } from './edit-affiliation.component';
import { EditAffiliationRoutingModule} from './edit-affiliation-routing.module';
import { DialogModule } from 'primeng/primeng';
import {BusyModule} from 'angular2-busy';
import {EditorModule} from 'primeng/primeng';

@NgModule({
  imports: [
    CommonModule, 
    FormsModule,
    HttpModule,
    EditAffiliationRoutingModule,
    DialogModule,
    EditorModule,
	  BusyModule
  ],
  declarations: [
    EditAffiliationComponent
  ]
})
export class EditAffiliationModule { }



