import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { NgForm, NgModel, NgControl, FormsModule } from '@angular/forms';
import { LoopBackAuth, BooksApi, ContainerApi, CityApi, Contractor_affiliation_masterApi } from '../../../../shared/loopback-sdk';
import { CookieService } from 'angular2-cookie/core';
import { Headers, Http, Response } from '@angular/http';
import { stringify } from 'querystring';
import { ShareService } from '../../../../providers/share-service/share-service';
declare var window: any;
declare var hostname: any;
declare var sdkPrefix;
@Component({
	templateUrl: 'edit-affiliation.component.html'
})
export class EditAffiliationComponent {
	public id: number;
	display: boolean = false; result: any;
	submitted: boolean = false;
	affiliationName: any;
	busy: any;
	userDetails: any;
	rolename: any;
	loading: boolean = false;
	affiliationStatus:boolean = false;
	bookImage:any;
	loginFlagToken:any;
	contractorCount : number = 0;
	municipalityCount : number = 0;
	certificateId : number = 0;
	equipmentPreviousStatus : boolean = false;
	

	editAffiliation={
		"certificate_id":0,
		"certificate_name":"",
		"status": 0,
		//"updated_date":0
	}
	constructor(private loopAuth: LoopBackAuth, private route: ActivatedRoute, private http: Http, private _cookieService: CookieService, private books: BooksApi, private router: Router, private containerApi: ContainerApi, private share: ShareService, private cityApi:CityApi,private affiliationApi:Contractor_affiliation_masterApi) {
		this.userDetails = this.loopAuth.getCurrentUserData();
		this.rolename = this.userDetails.userDet.roles;
		this.id = this.route.snapshot.params['id'];
		this.loginFlagToken = this._cookieService.get(sdkPrefix+'id');
	}

	ngOnInit(): void {
		this.loading = true;
		var dataArrObj = {
			"certificate_id":this.id
		}
		this.affiliationApi.getAffiliation(dataArrObj).subscribe(data=>{
			
			if(data){
				let result = data.result[0];
				this.certificateId = result.certificate_id;
				this.affiliationName = result.certificate_name;
				this.affiliationStatus = (result.status)?true:false;
				this.contractorCount = result.total;
				//console.log("contractorCount ",this.contractorCount);
				this.editAffiliation={
					"certificate_id":result.certificate_id,
					"certificate_name": result.certificate_name,
					"status": result.status,
					//"updated_date":result.updated_date
				}


			}
			this.loading = false;
		},
		err=>{
			//console.log("err", err);
			this.loading = false;
		},
		()=>{

		});
	}

	goToListing() {
		// window.location.reload();
		this.router.navigate(["affiliation"]);
	}

	async submit(affiliationForm) {
		this.submitted = true;
		if (affiliationForm.valid) {
			this.loading = true;

			let date = new Date();
			let currentdate = date.getTime();

			this.editAffiliation.certificate_id = this.certificateId;
			this.editAffiliation.certificate_name = this.capital_letter(this.affiliationName);
			this.editAffiliation.status = (this.affiliationStatus)?1:0;
			//this.editAffiliation.updated_date = currentdate;

			

			//updating equipments-master
		
			this.ifNameExist(this.editAffiliation).then((total)=>{
				if(total > 0){
					this.loading = false;
					this.result = "Affiliation name already exist";
					this.display = true;
				}else{
					this.checkCount(this.editAffiliation).then((total)=>{
						if(total > 0 && !this.editAffiliation.status){
							this.loading = false;
							this.result = "Affiliation is already been used so it cannot be inactive";
							this.display = true;
						}else{
							this.affiliationApi.upsertPatch(this.editAffiliation).subscribe((data)=>{
								this.loading = false;
								this.result = "success";
								this.display = true;
							},(err)=>{
								this.loading = false;
								this.result = "Something went wrong";
								this.display = true;
							});
						}
					},(err)=>{
						this.loading = false;
						this.result = "Something went wrong";
						this.display = true;
					});
				}
			},(err)=>{
				this.loading = false;
				this.result = "Something went wrong";
				this.display = true;
			});

			
		}
	}

	ifNameExist(dataObj){
		return new Promise((resolve,reject)=>{
			try{

				this.affiliationApi.getAffiliationCountName({ "certificate_name": dataObj.certificate_name,"status":dataObj.status}).subscribe((data)=>{
					resolve(data.result[0].total);
		
					},err=>{
						reject(err);
				});
			
			}catch(err){
				reject(err);
			}
		});
		
		
	}

	checkCount(dataObj){
		return new Promise((resolve,reject)=>{
			try{
				let access_token = this.loopAuth.getAccessTokenId();
				let certificate_id = dataObj.certificate_id;
				let cond = { "certificate_id": certificate_id};
				
				let api="/api/contractor_affiliation_mappings/count?where=";
				
				let url = hostname+api+encodeURI(JSON.stringify(cond))+"&access_token="+access_token;
				this.http.get(url).subscribe((data)=>{
					//console.log("Count ",data.json());
					resolve(data.json().count);
				},(err)=>{
					reject(err);
				});
			}
			catch(err){
				reject(true);
			}
		});
	}

	capital_letter(str) 
	{
		if(str != '' && str != null && str != undefined){
			str = str.split(" ");

			for (var i = 0, x = str.length; i < x; i++) {
				str[i] = str[i][0].toUpperCase() + str[i].substr(1);
			}
	
			return str.join(" ");
		}else{
			return str
		}
		
	}

	
}


